#resume {
    .resume-container {}
}

.adress-card-info {
    color: #052422;
    font-size: 110%;
    margin-top: 20px;
    .address-card-info-group {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        .fa {
            &.icon-location {
                font-size: 34px;
            }
            &.icon-phone {
                font-size: 26px;
            }
            &.icon-mail {
                font-size: 22px;
            }
        }
        .contact-info-text {
            margin: 0
        }
    }
}

.social {
    position: relative;
    height: 3em;
    width: 13.5em;
    margin: 70px auto;
    li {
        display: block;
        height: 4em;
        line-height: 4em;
        margin: -2.2em;
        position: absolute;
        -webkit-transition: -webkit-transform .7s;
        -moz-transition: -moz-transform .7s;
        -ms-transition: -ms-transform .7s;
        -o-transition: -o-transform .7s;
        transition: transform .7s;
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
        text-align: center;
        width: 4em;
        &.list-xing {
            background: #155b9d;
            left: 50%;
            top: 0%;
            padding-top: 5px;
        }
        &.list-linkedin {
            background: #1a9ec4;
            bottom: 0;
            left: 25%;
        }
        &.list-github {
            background: #3f7aa3;
            bottom: 0;
            left: 75%;
        }
        a {
            color: #fffdf0;
            display: inline-flex;
            align-items: center;
            height: 4em;
            line-height: 6em;
            text-align: center;
            -webkit-transform: rotate(-45deg);
            -moz-transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
            transform: rotate(-45deg);
            width: 4em;
            &:hover {
                -webkit-transform: scale(1.3, 1.3) rotate(45deg);
                -moz-transform: scale(1.3, 1.3) rotate(45deg);
                -ms-transform: scale(1.3, 1.3) rotate(45deg);
                -o-transform: scale(1.3, 1.3) rotate(45deg);
                transform: scale(1.3, 1.3) rotate(45deg);
            }
            i {
                margin: 0 auto
            }
        }
    }
}

.tool-list {
    .tool-item {
        p {
            font-size: 14px;
            b {
                font-size: 20px;
                font-weight: bold
            }
        }
    }
}

.contact-container {
    .card {
        min-height: 240px;
        .card-body {
            display: flex;
            padding: 0;
            justify-content: center;
            align-items: center;
        }
    }
}

.resume-mobile {
    display: none
}


/*********************
REACT TABS
*********************/

.react-tabs {
    -webkit-tap-highlight-color: transparent;
    width: 100%;
    display: flex;
    .tabs-menu {
        padding: 0
    }
    .tabs-content {
        padding: 0
    }
    &__tab-list {
        float: none;
        margin: 0;
        padding: 0;
        list-style: none;
        border: 0px;
        background-color: #3f51b5;
        width: 110px;
        z-index: 999;
        @media (max-width: 1200px) {
            text-align: center;
            margin: 0;
        }
    }
    &__tab {
        display: block;
        float: none;
        height: 80px;
        border-bottom: 1px solid white;
        color: #fff;
        &--selected {
            background: #fff;
            color: #3f51b5;
            font-weight: bold
        }
        &--disabled {
            color: red;
            cursor: default;
        }
        &:focus {
            outline: none;
            &:after {
                content: "";
                position: absolute;
                height: 5px;
                left: -4px;
                right: -4px;
                bottom: -5px;
                background: #fff;
            }
        }
        p {
            display: block;
            height: 80px;
            text-align: center;
            padding-top: 30px;
            font-weight: 600;
            margin: 0;
            cursor: pointer;
            &:hover {
                background: #ffffff24;
                color: #ededed
            }
        }
    }
    &__tab-panel {
        display: none;
        text-align: left;
        &--selected {
            display: block;
        }
    }
}


/*********************
END REACT TABS
*********************/

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {}

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {}

// Medium devices (tablets, less than 992px)
@media (max-width: 991px) {
    .resume-desktop {
        display: none
    }
    .resume-mobile {
        display: block
    }
}

