.social {
  position: relative;
  height: 3em;
  width: 13.5em;
  margin: 70px auto; }
  .social li {
    display: block;
    height: 4em;
    line-height: 4em;
    margin: -2.2em;
    position: absolute;
    -webkit-transition: -webkit-transform .7s;
    transition: -webkit-transform .7s;
    transition: transform .7s;
    transition: transform .7s, -webkit-transform .7s;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    text-align: center;
    width: 4em; }
    .social li.list-xing {
      background: #155b9d;
      left: 50%;
      top: 0%;
      padding-top: 5px; }
    .social li.list-linkedin {
      background: #1a9ec4;
      bottom: 0;
      left: 25%; }
    .social li.list-github {
      background: #3f7aa3;
      bottom: 0;
      left: 75%; }
    .social li a {
      color: #fffdf0;
      display: inline-flex;
      align-items: center;
      height: 4em;
      line-height: 6em;
      text-align: center;
      -webkit-transform: rotate(-45deg);
      transform: rotate(-45deg);
      width: 4em; }
      .social li a:hover {
        -webkit-transform: scale(1.3, 1.3) rotate(45deg);
        transform: scale(1.3, 1.3) rotate(45deg); }
      .social li a i {
        margin: 0 auto; }

