/*********************
REACT TABS
*********************/
.react-tabs {
  -webkit-tap-highlight-color: transparent;
  width: 100%;
  display: flex; }
  .react-tabs .tabs-menu {
    padding: 0; }
  .react-tabs .tabs-content {
    padding: 0; }
  .react-tabs__tab-list {
    float: none;
    margin: 0;
    padding: 0;
    list-style: none;
    border: 0px;
    background-color: #3f51b5;
    width: 110px;
    z-index: 999; }
    @media (max-width: 1200px) {
      .react-tabs__tab-list {
        text-align: center;
        margin: 0; } }
  .react-tabs__tab {
    display: block;
    float: none;
    height: 80px;
    border-bottom: 1px solid white;
    color: #fff; }
    .react-tabs__tab--selected {
      background: #fff;
      color: #3f51b5;
      font-weight: bold; }
    .react-tabs__tab--disabled {
      color: red;
      cursor: default; }
    .react-tabs__tab:focus {
      outline: none; }
      .react-tabs__tab:focus:after {
        content: "";
        position: absolute;
        height: 5px;
        left: -4px;
        right: -4px;
        bottom: -5px;
        background: #fff; }
    .react-tabs__tab p {
      display: block;
      height: 80px;
      text-align: center;
      padding-top: 30px;
      font-weight: 600;
      margin: 0;
      cursor: pointer; }
      .react-tabs__tab p:hover {
        background: #ffffff24;
        color: #ededed; }
  .react-tabs__tab-panel {
    display: none;
    text-align: left; }
    .react-tabs__tab-panel--selected {
      display: block; }

/*********************
END REACT TABS
*********************/
@media (max-width: 991px) {
  .resume-desktop {
    display: none; }
  .resume-mobile {
    display: block; } }


